import Link from 'next/link';
import { useRouter } from 'next/router';

export default function About() {
    const router = useRouter();
    let slug = router.asPath;
    // remove "/"
    slug = slug.substr(1);
    return (
      <div>
        <Link href="/"><a>Goto Index</a></Link>
        <h1>{slug}</h1>
        <p>This is a post!</p>
      </div>
    );
}